import request from '@/utlis/request'

// 已接单兼职列表
const getStudentList = async (data)=>{
    const studentlist = await request({
        method:'GET',
        url:'student',
        params:{...data}
    })
    return studentlist
}

// 已接单的兼职搜索
const getStudentSearch = async (data)=>{
    const studentSearch = await request({
        method:'GET',
        url:'student',
        params:{...data}
    })
    return studentSearch
}

export default  {
    getStudentList,
    getStudentSearch
}
