<template>
  <div class="user">
    <!--我的接单头部-->
    <div class="user-header">
      <el-form :inline="true" :model="studentForm" class="demo-form-inline">
        <el-form-item label="">
          <el-input class="user_input" v-model="studentForm.xq_name" placeholder="输入需求名称搜索"></el-input>
        </el-form-item>
        <el-form-item>
          <el-button class="user_search" type="primary" icon="el-icon-search" style="font-size: 14px" @click="handleSearch">搜索</el-button>
          <el-button class="user_refresh" type="primary" icon="el-icon-refresh" style="font-size: 14px" @click="handleRefresh">刷新</el-button>
        </el-form-item>
      </el-form>
    </div>

    <!--我的接单主体-->
    <el-table
        :data="tableData.slice((currentPage-1)*pagesize,currentPage*pagesize)"
        style="width: 100%">
      <el-table-column
          prop="jz_order_num"
          label="订单编号"
          width="200">
      </el-table-column>
      <el-table-column
          prop="xq_name"
          label="需求名称"
          width="300">
      </el-table-column>
      <el-table-column
          prop="description"
          label="需求描述"
          width="600">
      </el-table-column>
      <el-table-column
          prop="cycle_requiremente"
          label="周期要求"
          width="100">
      </el-table-column>
      <el-table-column
          prop="technical_requiremente"
          label="技术要求"
          width="200">
      </el-table-column>
      <el-table-column
          prop="creat_time"
          label="发布时间"
          width="120">
      </el-table-column>
      <el-table-column
          prop="budget"
          label="预算"
          width="100">
      </el-table-column>
    </el-table>

    <!-- 分页 -->
    <el-pagination
            @size-change="handleSizeChange"
            @current-change="handleCurrentChange"
            :current-page="currentPage"
            background
            :page-sizes="[20,30,50]"  
            :page-size="pagesize"         
            layout="total, sizes,  prev, pager, next, jumper" 
            :total="tableData.length">
        </el-pagination>
  </div>
</template>


<script>
import studentInfo from '@/services/student'
export default {
  name:'user',
  data(){
    return{
      studentForm:{
        xq_name:''

      },
      tableData: [{
        id:'',
        xq_name:'',
        description:'',
        cycle_requiremente:'',
        technical_requiremente:'',
        budget:''
      }],
      userInFo:{
        id:'',
        xq_name:'',
        description:'',
        cycle_requiremente:'',
        technical_requiremente:'',
        budget:''
      },
      dialogFormVisible:false,
      multipleSelection:[],
      multipleSelections:[],
      currentId:null,
      isAdd:true,
      totalCount:0,
      currentPage:1, // 初始页
      pagesize:20,  // 初始每页的数据
    }
  },
  created() {
    this.loadStudentList()
  },
  methods:{
    // 列表
    async loadStudentList() {
      const queryOption = {
        type: 'jz_list',
        account:localStorage.username,
        page: 1,
        size: 20
      }
      try {
        const { data: res } = await studentInfo.getStudentList(queryOption)
        if (res.code === '0') {
          this.tableData = res.data
          this.totalCount = res.total_count
        }
      } catch (error) {                       // 捕获axios抛出的401异常请求
        if (error.message === '未登录') {
          this.$router.push('/login')       // 跳转至登录页
        } else {
          // 处理其他错误
          this.$message.success('登陆失效，请重新登陆')
          this.$router.push('/login')       // 跳转至登录页
        }
      }
    },
    // 需求名称搜索
    async handleSearch() {
      const searchOptions = {
        xq_name:this.studentForm.xq_name,
        type:'xq_name'
      }
      const {data:res} = await studentInfo.getStudentSearch(searchOptions)
      if (res.code==='0'){
        this.tableData = res.data
        this.studentForm.xq_name=''
      }
    },
    // 刷新
    handleRefresh(){
      this.loadStudentList()
      this.studentForm.xq_name=''
    },
    // 初始页currentPage、初始每页数据数pagesize和数据data
    handleSizeChange: function (size) {
                this.pagesize = size;
                // console.log(this.pagesize)  //每页下拉显示数据
        },
        handleCurrentChange: function(currentPage){
                this.currentPage = currentPage;
                // console.log(this.currentPage)  //点击第几页
        }
  }
}
</script>


<style lang="scss" scoped>
.user_input{
  margin-right: 20px;
}
.user_refresh{
  margin: 0 2px;
}
.user_search{
  margin: 0 28px;
}
.user-header{
  height: 55px;
}
</style>
